/*----------------------------------------------*/
/* Base Style */
/*----------------------------------------------*/
body {
	font-family: "Open Sans", sans-serif;
	/* background: url(/bg.png) center bottom; */
	background-size: cover;
	background-color: #e8e8e8;
	margin: 0px;
	padding: 0px;
	font-size: 14px;
	color: #111;
	width: 100%;
	height: 100%;
	overflow: hidden;
	min-height: 100vh;
	background-repeat: no-repeat;
}

.h1,.h2,.h3,h1,h2,h3 {
	font-family: "Oswald", sans-serif;
	font-weight: 400;
}

/*----------------------------------------------*/
/* Wrapper */
/*----------------------------------------------*/
.app {
	display: flex;
	align-items: center;
}
.container-padding{
	padding: 15px 30px;;
}
.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgb(181, 218, 193);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}
#frontpage {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	transition: all 1s ease-in-out;
  }
  
  #frontpage.animate-hide {
	transform: scale(0.3) translate(-200%, -200%);
	opacity: 0;
  }
.container {
	width: 100%;
	max-width: 1000px;
	position: relative;
	padding-bottom: 15px;
}

#content {
	position: relative;
	min-height: 720px;
}

.copyright-text {
	float: right;
	margin-right: 15px;
	margin-top: 0px;
}

/*-------------------------------------------------------*/
/* Generic Classes */
/*-------------------------------------------------------*/
.abs-child {
	position: relative;
}

.hidden {
	display: none;
}

.clear {
	clear: both;
}

.large-paragraph {
	font-size: 14px;
	line-height: 1.7em;
	letter-spacing: 0.02em;
}

.large-paragraph,
.focus-text {
	color: #666666;
}

.no-padding {
	list-style: none;
	padding-left: 0px;
}

.shadow-img {
	position: absolute;
	top: 0px;
	left: 0px;
	right: 0px;
	bottom: 0px;
	background: #111111;
	background-size: 100% 100%;
	z-index: 0;
}

.right-align {
	text-align: right;
}

.center-align {
