/*-------------------------------------------------------*/
/* Namecard Design Section */
/*-------------------------------------------------------*/
#namecard {
	position: relative;
	background-color: #666;
	height: 50%;
	min-height: 300px;
	text-align: center;
	padding: 15px;
	margin: 0px;
	margin-bottom: 15px;
}

.namecard,
.namecard h1 {
	font-family: "Oswald", sans-serif;
}

.profile-img {
	position: relative;
	width: 140px;
	height: 140px;
	border-radius: 70px;
	float: none;
	overflow: hidden;
	background-color: #fff;
	border: 7px solid rgb(181, 218, 193);
	opacity: 1;
	/* Modern!Firefox 0.9+, Safari 2?, Chrome any?Opera 9+, IE 9+ */
	-khtml-opacity: 1;
	-moz-opacity: 1;
	/* Older than Firefox 0.9 */
	filter: alpha(opacity=100);
	/* This works in IE 8 & 9 too, but also 5, 6, 7 */

	transition: opacity 1s ease-in-out;
	-moz-transition: opacity 1s ease-in-out;
	-webkit-transition: opacity 1s ease-in-out;
}

/*-------------------------------------------------------*/
/* Menu-Container Design Section */
/*-------------------------------------------------------*/
#menu-container {
	position: relative;
	display: block;
	clear: both;
	background-color: rgb(181, 218, 193);
	height: 50%;
	min-height: 305px;
	text-align: right;
	padding: 15px 0px;
	margin: 0px;
}

ul.nav-menu li {
	position: relative;
	padding: 12px 15px;
	width: 100%;
	color: #111111;
	border-bottom: 1px solid #e1c03a;
	font-size: 16px;
	margin: 0px;
	font-family: "Oswald", sans-serif;
	cursor: pointer;
	overflow: hidden;
}

.hover-background {
	position: absolute;
	background-color: #111111;
	z-index: -1;
	width: 300%;
	height: 300%;
	top: 200%;
	left: -100%;
	-ms-transform: rotate(15deg);
	-webkit-transform: rotate(15deg);
	-moz-transform: rotate(15deg);
	transform: rotate(15deg);
	-webkit-transition: top 0.5s, left 0.5s;
	-moz-transition: top 0.5s, left 0.5s;
	-o-transition: top 0.5s, left 0.5s;
	transition-timing-function: ease-out;
	transition: top 0.5s, left 0.5s;
}
.nav-btn > span,
.nav-btn > i {
  position: relative;
  z-index: 1;
}
ul.nav-menu li:hover {
	border-bottom-color: #111111;
	color: rgb(181, 218, 193);
}

ul.nav-menu li:hover .hover-background,
ul.nav-menu li.selected .hover-background {
	top: 0%;
	left: -15%;
	z-index: 0;
}

ul.nav-menu li.selected {
	background-color: #111111;
	border-bottom-color: #111111;
	border-right: none;
	color: rgb(181, 218, 193);
}

ul.nav-menu li span {
	padding-right: 25px;
	border-right: 2px solid #e1c03a;
	margin-right: 15px;
	text-transform: uppercase;
}

ul.nav-menu li:hover span,
ul.nav-menu li.selected span {
	border-right: 2px solid rgb(181, 218, 193);
}

.social-menu-container {
	position: absolute;
	bottom: 0px;
	right: 0px;
	left: 0;
	text-align: center;
}

.social-menu-container .title {
	margin-bottom: 5px;
	padding-right: 15px;
	color: #111111;
}

.social-menu {
	display: inline-block;
	float: none;
	font-size: 18px;
	margin-right: 5px;
}

.social-menu a {
	color: #111111;
}

.social-menu a:hover {
	color: rgb(181, 218, 193);
}

.social-menu li {
	float: left;
	width: 40px;
	height: 40px;
	text-align: center;
	border-radius: 20px;
	cursor: pointer;
	transition: all 0.1s ease-in;
	-webkit-transition: all 0.1s ease-in;
}

.social-menu li:hover {
	background-color: #111111;
	color: rgb(181, 218, 193);
}

.social-menu li:hover a {
	color: rgb(181, 218, 193);
}

.social-menu li i {
	margin-top: 33%;
}

