/*-------------------------------------------------------*/
/* Description Design Section */
/*-------------------------------------------------------*/
.description {
	height: 475px;
	position: relative;
	background-color: #fff;
	padding: 15px 30px;
	overflow: auto;
}

.description p {
	margin: 15px 0px;
	padding-right: 75px;
}

.strong-text {
	font-family: "Oswald", sans-serif;
	display: inline-block;
	font-size: 22.5px;
	margin: 10px 0px;
}

